export const layoutMixins = {
  data() {
    return {
      height: 600
    }
  },
  created() {
    this.$nextTick(() => {
      this.windowResize()
      window.addEventListener('resize', this.windowResize, false)
    })
  },
  methods: {
    windowResize() {
      const menu = document.getElementById('menu'), head = document.getElementById('head');
      if (menu) {
        this.height = menu.offsetHeight  -  head.offsetHeight;
        this.$store.commit('SaveScreenHeight', this.height)
      }
      // console.log(`screen height: [${this.height}px]`)

    }
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.windowResize, false)
  }
}



