<template>
  <div class="app-layout">
    <HeadEe class="head" id="head"/>
    <Menu class="menu" id="menu"/>
    <div class="app-layout-content">
      <!--<el-scrollbar :style="{height: `${height}px`}">-->
        <router-view/>
      <!--</el-scrollbar>-->
      <!--<router-view/>-->
    </div>
  </div>
</template>

<script>
  import HeadEe from './components/headee'
  import Menu from './components/menu'
  import {layoutMixins} from './components/setScrollBarHeight'
  export default {
    components: {HeadEe, Menu},
    mixins: [layoutMixins]
  }
</script>

<style scoped rel="stylesheet/scss" lang="scss">
  .app-layout {
    width: 100%;
    height: 100%;
    .head {
      position: fixed;
      z-index: 10;
      left: 0;
      top: 0;
      width: 100%;
      height: 0.56rem;
      padding-left: 1.75rem;
    }
    .menu {
      position: fixed;
      z-index: 11;
      left: 0;
      top: 0;
      width: 1.6rem;
      height: 100%;
      /*background: #F2F6FC;*/
    }
    .app-layout-content {
      width: 100%;
      height: 100%;
      /*padding: 0.56rem 0 0 1.75rem;*/
      padding: 0.76rem 0.2rem 0.2rem 1.95rem;
      background: #EBEEF5;
    }
  }
</style>



