<template>
  <div class="not-found" :style="style" id="body">404<div id="test-height" class="test-height"></div></div>
</template>

<script>
  export default {
    data(){
      return {
        style: {}
      }
    },
    mounted(){
      const height = document.getElementById('test-height').offsetHeight;
      // Object.assign(this.style, {lineHeight: `${height}px`, fontSize:`${height/3}px`})
      // this.$refs.body.style.lineHeight = `${height}px`
      // document.getElementById('body').style.lineHeight = `${height}px`
      Object.assign(document.getElementById('body').style, {lineHeight: `${height}px`, fontSize:`${height/3}px`})
      console.log(height)
    }
  }
</script>

<style scoped rel="stylesheet/scss" lang="scss">
.not-found{
  width: 100%;
  height: 100%;
  line-height: 1;
  color: red;
  font-size: 300px;
  text-align: center;
  .test-height{
    position: fixed;
    left: 0;
    top: 0;
    background: transparent;
    width: 1px;
    height: 100%;
  }
}
</style>



