import { asyncMap } from '@/router/routeMap'

const filterRoutes = (routeMap, pathPrefix) => {
  return routeMap.map(({path, children, meta}) => {
      let route = {path}
      if(meta){
        const { title = 'title'} = meta ;
        Object.assign(route, {title})
      }
      if(children){
        Object.assign(route, {child:filterRoutes(children)})
      }else{

      }
      return route;
  })
}

const parseRoutesMap = (routeList) => filterRoutes(routeList.filter(({hidden}) => !hidden))
export default {
  state: {
    routeMap: [],
    openCollapse: false
  },
  mutations: {
    SaveRouteMap(state, routeMap){
      Object.assign(state, {routeMap})
    },
    CloseMenu(state, openCollapse){
      Object.assign(state, {openCollapse})
    }
  },
  actions: {
    ParseRouteMap({commit}){
      const maps = parseRoutesMap(asyncMap)
      commit('SaveRouteMap', maps)
    }
  }
}



