<template>
  <div class="app-layout-head">
    <button  type="button" @click="closeMenu" :class="{'button': true, 'close': openCollapse}"><i class="el-icon-tickets icon"></i></button>
    <span class="title">{{ title }}</span>
    <button  type="button" class="button" @click="onToggleFullScreen"><i class="el-icon-rank icon"></i></button>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import { testFullScreenMixins } from './fullscreen'
  export default {
    mixins: [ testFullScreenMixins ],
    computed: {
      ...mapGetters(['openCollapse']),
      title(){
        return this.$route.meta.title||'title'
      }
    },
    methods: {
      closeMenu(){
        this.$store.commit('CloseMenu', !this.openCollapse)
      }
    }
  }
</script>

<style scoped rel="stylesheet/scss" lang="scss">
    $borcolor: #909399;
    .app-layout-head{
      /*background:#183250; #303133*/
      background:#FFF;
      width: 100%;
      height: 0.56rem;
      font-size: 0.2rem;
      color:#ffffff;
      border: 0.03rem solid transparent;
      display: flex;
      &:hover{
        /*background: #909399;*/
        animation: border-ani 0.5s;
      }
      .title, .button{
        display:inline-block;
        width: 1rem;
        height: 0.5rem;
        line-height: 0.5rem;
        background: transparent;
        border: 0;
        outline: none;
        vertical-align: middle;
        font-size: 0.4rem;
        color: #409EFF;
        transition: transform 0.2s;
        text-align: center;
        &.close{.icon{transform: rotate(90deg);}}
      }
      .button{flex: 1;cursor: pointer;}
      .title{flex: 5;font-size: 0.25rem;cursor: alias;}
    }
  @keyframes border-ani {
    0%{border-left: 0.03rem solid $borcolor}
    25%{border-top: 0.03rem solid $borcolor}
    75%{border-right: 0.03rem solid $borcolor}
    100%{border-bottom: 0.03rem solid $borcolor}
  }
</style>



