<template>
  <div class="app-layout-menu">
    <el-menu default-active="2" class="menu-content" :collapse="openCollapse"
      @open="handleOpen" @close="handleClose"
      background-color="#545c64" text-color="#ffe" active-text-color="#ffd04b">

      <el-submenu  v-for="(item, index) in routeMap" :index="`${index}`" :key="`${index}fa`">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>{{ item.title }}</span>
        </template>

        <div class="item-route" v-for="(route, i) in item.child" :key="`${i}s`">

          <el-submenu :index="`${index}-${i}`" v-if="!!route.child">
            <template slot="title">{{ route.title }}</template>
            <el-menu-item v-for="(router, k) in route.child" :index="`${index}-${i}-${k}`" :key="`${k}ms`">
              <!--{{ // router.title }}-->
              <router-link  tag="div" :to="setPath(item.path, route.path, router.path)">{{ router.title }}</router-link>
            </el-menu-item>
          </el-submenu>

          <el-menu-item :index="`${index}-${i}`" v-else>
            <!--<span slot="title">{{ route.title }}</span>-->
            <router-link slot="title" tag="div" :to="setPath(item.path, route.path)">{{ route.title }}</router-link>
          </el-menu-item>

        </div>

      </el-submenu>

    </el-menu>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    computed: {
      ...mapGetters(['openCollapse', 'routeMap'])
    },
    mounted(){
      // console.log(JSON.stringify(this.routeMap, null, 1))
    },
    methods: {
      setPath(...args){
        return args.join('/').replace(/\/{2}/g, '')
      },
      handleOpen(key, keyPath) {
        // console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        // console.log(key, keyPath);
      }
    }
  }
</script>

<style scoped rel="stylesheet/scss" lang="scss">
.app-layout-menu{
  background: #545c64;
  overflow: hidden;
  .menu-content{
    width: 100%;
    .el-menu-item{
      min-width: 0;
      width: 1.6rem;
    }
    .el-submenu__title{
      width: 1.6rem;
    }
    .el-submenu{
      width: 1.6rem;
    }
  }
}
</style>



