// 判断是否处在全屏的状态
const checkIsFullScreen = () => {
  return window.screen.height - window.outerHeight < 20;
};
// 进入全屏和退出全屏的方法
const requestFullScreenEvent =() => requestFullScreen(document.documentElement);// 整个网页
function requestFullScreen(element) {
  // 判断各种浏览器，找到正确的方法
  const requestMethod = element.requestFullScreen || //W3C
    element.webkitRequestFullScreen ||    //Chrome等
    element.mozRequestFullScreen || //FireFox
    element.msRequestFullScreen; //IE11
  if (requestMethod) {
    requestMethod.call(element);
  }
  else if (typeof window.ActiveXObject !== "undefined") {//for Internet Explorer
    const wScript = new ActiveXObject("WScript.Shell");
    if (wScript !== null) {
      wScript.SendKeys("{F11}");
    }
  }
}

//退出全屏 判断浏览器种类
export function exitFull() {
  // 判断各种浏览器，找到正确的方法
  const exitMethod = document.exitFullscreen || //W3C
    document.mozCancelFullScreen ||    //Chrome等
    document.webkitExitFullscreen || //FireFox
    document.webkitExitFullscreen; //IE11
  if (exitMethod) {
    exitMethod.call(document);
  }
  else if (typeof window.ActiveXObject !== "undefined") {//for Internet Explorer
    const wScript = new ActiveXObject("WScript.Shell");
    if (wScript !== null) {
      wScript.SendKeys("{F11}");
    }
  }
}

const disabledExitFullCodes = [ 122, 27, '122', '27'];

export const testFullScreenMixins = {
  data() {
    return {
      isFullScreen: false,
      fullScreenButtonId: 'full-screen-button-'+ (+new Date())
    }
  },
  mounted() {
    // document.getElementById(this.fullScreenButtonId).click();
    this.onScreenResize();
    window.addEventListener('resize', this.onScreenResize, false);
    // document.addEventListener('keyup', this.onKeyUp, false);
  },
  methods: {
    onScreenResize() {//监听屏幕高度的变化
      this.isFullScreen = checkIsFullScreen();
    },
    onToggleFullScreen() {
      this.isFullScreen ? exitFull() :　requestFullScreenEvent();
    },
    onKeyUp(e){
      if(disabledExitFullCodes.includes(e.keyCode)){
        requestFullScreenEvent();
        // console.log('----------------------------------')
      }
      return false;
    },
  },
  beforeDestroy(){
    window.removeEventListener('resize', this.onScreenResize, false);
    // document.removeEventListener('keyup', this.onKeyUp, false);
  }
};

