import r404 from '@/views/r404/index'

const _import = (path) => resolve => require([`@/views/${path}`], resolve)
import Layout from '@/views/layout/index'

const Hidden = {hidden: true}

export const asyncMap = [
  {
    path: '/',
    component: Layout,
    meta: {title: 'home'},
    children: [
      {path: '/', component: _import('home/index'), meta: {title: '首页'}},
      {path: 'capture', component: _import('home/capture'), meta: {title: '试试拍照'}},
      {path: 'map', component: _import('home/outdoorMap'), meta: {title: '显示地图'}},
    ]
  },
  {path: '/404', component: r404, ...Hidden},
  {
    path: '/date',
    component: Layout,
    meta: {title: 'date'},
    children: [
      {
        path: '/',
        component: _import('date/index'),
        meta: {title: '日期组件'},
        children: [
          {path: '', component: _import('date/page/time'), meta: {title: '时间组件'}},
          {path: 'date', component: _import('date/page/date'), meta: {title: '组件日期'}},
        ]
      },
      {path: 'config', component: _import('date/config'), meta: {title: '配置'}},
    ]
  },
  {path: '*', redirect: '/404', ...Hidden}
]



